import React, {useState,useEffect} from 'react'
import "../node_modules/bootstrap/dist/css/bootstrap.min.css"
import "./App.css"

function App() {
  
  const [data,setData]=useState([]);

  const getCovidData =  async ()=>{
    try {
      const res = await fetch("https://api.covid19india.org/data.json");
      const actualData = await res.json();
      setData(actualData.statewise[0]);
    } catch (error) {
      console.log(error)
    }

  }
  useEffect(()=>{
    getCovidData();
  },[]);


  return (
   <>
   <div className="data"  >
   <div className="live" >

   <h2>ðŸ”´ Live</h2>
   </div>

    <div className="container" >
   <h1  style={{fontSize:'40px',fontFamily:"fantasy",color:"wheat"}} >  <span style={{color:'#8A2BE2'}} >COVID-19</span> &nbsp;TRACKER</h1>

   <div className="card" style={{backgroundColor:'#fed8b1',boxShadow: '5px 6px #fed8b1'}}  >
   <div className="card-body">
    <p className="card-text"  ><sub>OUR</sub>  &nbsp; <span style={{fontSize:"2rem",fontWeight:'bold'}} >COUNTRY</span> </p>
    <h5 className="card-title" style={{color:'#fed8b1'}} >INDIA</h5>
  </div>
  </div>

  <div className="card" style={{backgroundColor:'#00ff00',boxShadow: '5px 6px #00ff00 '}}  >
   <div className="card-body">
    <p className="card-text"><sub>TOTAL</sub>  &nbsp; <span style={{fontSize:"2rem",fontWeight:'bold'}} >RECOVERED</span> </p>
    <h5 className="card-title" style={{color:'#00ff00'}} >{data.recovered}</h5>
  </div>
  </div>

  <div className="card" style={{backgroundColor:'yellow',boxShadow: '5px 6px yellow'}}  >
   <div className="card-body">
    <p className="card-text"><sub>TOTAL</sub>  &nbsp; <span style={{fontSize:"2rem",fontWeight:'bold'}} >CONFIRMED</span> </p>
    <h5 className="card-title" style={{color:'yellow'}} >{data.confirmed} </h5>
  </div>
  </div>

  <div className="card" style={{backgroundColor:'red',boxShadow: '5px 6px red'}}  >
   <div className="card-body">
    <p className="card-text"><sub>TOTAL</sub>  &nbsp; <span style={{fontSize:"2rem",fontWeight:'bold'}} >DEATHS</span> </p>
    <h5 className="card-title" style={{color:'red'}} >{data.deaths} </h5>
  </div>
  </div>

  <div className="card" style={{backgroundColor:'blue',boxShadow: '5px 6px blue'}}  >
   <div className="card-body">
    <p className="card-text"><sub>TOTAL</sub>  &nbsp; <span style={{fontSize:"2rem",fontWeight:'bold'}} >ACTIVE</span> </p>
    <h5 className="card-title" style={{color:'blue'}} >{data.active} </h5>
  </div>
  </div>

  <div className="card" style={{backgroundColor:'orange',boxShadow: '5px 6px orange'}}  >
   <div className="card-body">
    <p className="card-text"><sub>LAST</sub>  &nbsp; <span style={{fontSize:"2rem",fontWeight:'bold'}} >UPDATED</span> </p>
    <h5 className="card-title" style={{color:'orange'}} >{data.lastupdatedtime} </h5>
  </div>
  </div>

  <div  className="card"  style={{backgroundColor:'#A52A2A',boxShadow: '5px 6px #A52A2A'}} >
   <div className="card-body">
    <p className="card-text">Developed by </p>
    <h5 className="card-title" style={{color:'#A52A2A'}} >Ashutosh Mishra</h5>
  </div>
  </div>
  </div>
   </div>
   </>
  );
}

export default App;
